















































































































































































.schedule-name-list-wrapper {
  max-height: 30vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.schedule-name-list-wrapper::-webkit-scrollbar {
  width: 10px;
}

.schedule-name-list-wrapper::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}

.schedule-name-list-wrapper::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 6px;
}

.schedule-name-list-wrapper::-webkit-scrollbar-thumb:hover {
  background: white;
}
